import Vue from 'vue'
import Vuex from 'vuex'
import base from './base'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    loading: false,
    collapsed: false,
    user: {},
    visitedViews: JSON.parse(window.sessionStorage.getItem('visitedViews') || '[]'),
    cachedViews: JSON.parse(window.sessionStorage.getItem('cachedViews') || '[]')
  },
  mutations: {
    toggleCollapse(state) {
      state.collapsed = !state.collapsed
    },
    getUser(state) {
      const user = window.sessionStorage.getItem('user') || '{}'
      state.user = JSON.parse(user)
    },
    updateLoading(state, status) {
      state.loading = status
    },
    ADD_VISITED_VIEWS(state, view) {
      // if (state.visitedViews.some(v => v.path === view.path)) {
      //   return;
      // }
      if (view.path === '/router_temp') {
        return;
      }
      for (const [i, v] of state.visitedViews.entries()) {
        if (v.path === view.path) {
          if (view.meta.noCache) { //  如果是详情页且在visitedViews中,替换它
            state.visitedViews.splice(i, 1, view);
            return;
          } else {
            return;
          }
        }
      }
      state.visitedViews.push(view);
      state.cachedViews.push(view.name)
      if (state.visitedViews.length > 10) {
        state.visitedViews.shift();
        state.cachedViews.shift();
      }
      window.sessionStorage.setItem('cachedViews', JSON.stringify(state.cachedViews))
      window.sessionStorage.setItem('visitedViews', JSON.stringify(state.visitedViews))
    },
    DEL_VISITED_VIEWS(state, view) {
      let index;
      for (const [i, v] of state.visitedViews.entries()) {
        if (v.path === view.path) {
          index = i;
          break;
        }
      }
      state.visitedViews.splice(index, 1)
      for (const i of state.cachedViews) {
        if (i === view.name) {
          const index = state.cachedViews.indexOf(i)
          state.cachedViews.splice(index, 1)
          break
        }
      }
      window.sessionStorage.setItem('cachedViews', JSON.stringify(state.cachedViews))
      window.sessionStorage.setItem('visitedViews', JSON.stringify(state.visitedViews))
    }
  },
  actions: {
    addVisitedViews({ commit }, view) {
      commit('ADD_VISITED_VIEWS', view)
    },
    delVisitedViews({ commit, state }, view) {
      return new Promise((resolve) => {
        commit('DEL_VISITED_VIEWS', view)
        resolve([...state.visitedViews])
      })
    }
  },
  modules: {
    base
  }
});

export default store
