<template>
  <div class="main-container">
    <div class="main-header">
      <i class="fa fa-navicon menu-icon" @click="toggleCollapse"></i>
      <el-breadcrumb v-if="$route.meta.bread" class="breadcrumb" separator="/">
        <el-breadcrumb-item :key="key+''" v-for="(item,key) in $route.matched">{{ item.meta.title }}</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="main-header-user">
        <el-dropdown @command="handleClick">
          <span class="el-dropdown-link">
            {{name}}<i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
   <!--  <div class="main-nav">
      <el-tag size="mini" closable>{{$route.meta.title}}</el-tag>
      <el-tag size="mini" closable>{{$route.meta.title}}</el-tag>
    </div> -->
    <Tabs v-show="isShowTabs"></Tabs>
    <div class="main-body">
      <div class="main-body-content">
        <transition name="fade">
          <keep-alive :include="cachedViews">
            <router-view :key="$route.path" v-if="!$route.meta.noCache"></router-view>
          </keep-alive>
          <router-view v-if="$route.meta.noCache"></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
import Tabs from './tabs.vue'
export default {
  data() {
    return {}
  },
  components: {
    Tabs
  },
  mounted() {
    this.$store.commit('getUser')
  },
  computed: {
    name() {
      return this.$store.state.user.name;
    },
    isShowTabs() {
      return !!this.$store.state.visitedViews.length;
    },
    cachedViews() {
      return this.$store.state.cachedViews;
    }
  },
  methods: {
    toggleCollapse() {
      this.$store.commit('toggleCollapse')
    },
    logout() {
      this.$confirm('确认退出吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.$router.push('/login');
      }).catch(() => {});
    },
    handleClick(val) {
      switch (val) {
        case 'logout':
          this.logout();
          break;
      }
    }
  }
}
</script>
<style scoped lang="less">
@import '~style/var.less';
.main-header {
  position: relative;
  height: 50px;
  background-color: #324157;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  .menu-icon {
    display: inline-block;
    float: left;
    font-size: 24px;
    color: #fff;
    cursor: pointer;
    line-height: 50px;
    height: 50px;
    margin: 0 15px 0 5px;
    vertical-align: middle;
    transform: rotate(90deg);
  }
}

.hide-sidebar .main-header .menu-icon {
  transform: rotate(0deg);
  transition: transform 0.3s ease;
}

.main-header-user {
  position: absolute;
  right: 20px;
  top: 0;
  height: 50px;
  line-height: 50px;
  .el-dropdown-link {
    color: #fff;
    cursor: pointer;
  }
}

.main-body {
  box-sizing: border-box;
  position: relative;
  padding: 10px;
  height: calc(~ '100vh - 80px');
  z-index: 1;
  overflow: auto;
}
.main-body-content {
  overflow: auto;
  min-width: 1000px;
}
.breadcrumb {
  float: left;
  height: 50px;
  line-height: 50px;
}

</style>
