<template>
  <div class='main-nav'>
    <router-link class="tabs-view" v-for="tag in Array.from(visitedViews)" :to="{ path: tag.path, query: tag.query}" :key="tag.realPath">
      <el-tag :closable="closable" :type="isActive(tag)?'primary':''" @close='closeViewTabs(tag,$event)'>
        {{tag.title}}
      </el-tag>
    </router-link>
    <!-- <el-button type="primary" @click="refresh" size="small" style="padding:6px 15px;position:fixed;right:38px;top:52px"><i class="fa fa-refresh fa-lg"></i></el-button> -->
  </div>
</template>
<script>
export default {
  data() {
      return {}
    },
    computed: {
      visitedViews() {
        return this.$store.state.visitedViews;
      },
      closable() {
        return this.$store.state.visitedViews.length > 1;
      }
    },
    methods: {
      closeViewTabs(view, $event) {
        this.$store.dispatch('delVisitedViews', view).then((views) => {
          if (this.isActive(view)) {
            const latestView = views.slice(-1)[0]
            if (latestView) {
              this.$router.push({
                path: latestView.path,
                query: latestView.query
              })
            } else {
              this.$router.push('/')
            }
          }
        })
        $event.preventDefault()
      },
      generateRoute() {
        const realPath = window.location.hash.slice(1)
        const title = this.$route.meta.title;
        return {
          title: title,
          name: this.$route.name,
          path: this.$route.path,
          query: this.$route.query,
          realPath: realPath,
          meta: this.$route.meta
        };
      },
      addViewTabs() {
        this.$store.dispatch('addVisitedViews', this.generateRoute())
      },
      isActive(tag) {
        return tag.path === this.$route.path;
      },
      refresh() {
        console.log('refresh');
        if (!this.$route.meta.noCache) {
          for (const [i, v] of this.$store.state.visitedViews.entries()) {
            if (v.path === this.$route.path) {
              this.$store.state.cachedViews.splice(i, 1);
              this.$store.state.visitedViews.splice(i, 1);
              break;
            }
          }
        }
        this.$router.push({
          path: '/router_temp'
        })
      }
    },
    watch: {
      $route() {
        this.addViewTabs()
      }
    }
}
</script>
<style lang="less" scoped>
@import '~style/var.less';
.main-nav {
  height: 30px;
  .shadow();
}

.el-tag {
  cursor: pointer;
  background-color: #efefef;
  margin: 3px;
  &.el-tag--primary {
    background-color: #20a0ff;
    color: #fff;
    /deep/ .el-icon-close {
      color: #fff!important;
    }
  }
  
}
</style>
