import Layout from '@/views/layout/index.vue'
import Home from '@/views/home'
import NotFound from '@/views/404'
import Login from '@/views/login'

// 开发环境不使用懒加载, 因为懒加载页面太多的话会造成webpack热更新太慢, 所以只有生产环境使用懒加载
function _import(url) {
  if (process.env.NODE_ENV === 'development') {
    return require(`@/views${url}.vue`).default
  } else {
    return url => () => import(`@/views${url}.vue`)
  }
}

const routes = [{
  path: '/',
  name: '',
  title: '首页',
  component: Layout,
  bread: false,
  leaf: true,
  icon: 'fa fa-home fa-lg',
  children: [{
    path: '/home',
    component: Home,
    name: 'Home',
    title: '首页',
    bread: false
  }, {
    "path": "/404",
    "component": NotFound,
    "name": "404"
  }]
}, {
  path: '/login',
  component: Login,
  name: 'Login',
  title: '首页',
  hidden: true
}, {
  path: '/',
  name: 'User',
  title: '用户',
  component: Layout,
  bread: true,
  icon: 'fa fa-user fa-lg',
  children: [{
    path: '/user/index',
    component: _import('/user/index'),
    name: 'UserIndex',
    title: '用户列表',
    bread: true
  }]
}, {
  path: '/',
  title: '分类',
  name: 'Menu',
  component: Layout,
  bread: true,
  icon: 'fa fa-table fa-lg',
  children: [{
    path: '/demo/echarts',
    component: _import('/demo/echarts'),
    name: 'DemoEcharts',
    title: '图表',
    bread: true
  }, {
    path: '/demo/form',
    component: _import('/demo/form'),
    bread: true,
    title: '表单',
    name: 'DemoForm'
  }, {
    path: '/demo/editor',
    component: _import('/demo/editor'),
    bread: true,
    title: '编辑',
    name: 'DemoEditor'
  }]
}]

export default routes
