import Mock from 'mockjs';

var tableData = Mock.mock({
  'user|6-12': [{
    'name|+1': [
      'Peter',
      'Tom',
      'Lucy',
      'Jack'
    ],
    'address|+1': [
      Mock.mock('@city(true)'),
      Mock.mock('@city(true)'),
      Mock.mock('@city(true)')
    ],
    'gender|+1': [
      0, 1
    ],
    'date|+1': [
      Mock.mock('@datetime'),
      Mock.mock('@datetime'),
      Mock.mock('@datetime')
    ]
  }]
});

Mock.mock(/\/api\/user\/list/, 'get', tableData.user);

export default Mock;
